import { Slider } from '@mui/material';
import Text from '../../UI/atoms/Text';
import Flex from '../../UI/atoms/Flex';
import { Colors } from '../../../styles/colors';

interface StepSliderProps {
  text: string;
  textColor: Colors;
  values: string[];
  marks: {
    value: number;
    label: string;
  }[];
  handleChange: (
    event: Event,
    value: number | number[],
    activeThumb: number,
  ) => void;
  defaultValue: string;
}

export default function StepSlider({
  text,
  textColor,
  values,
  marks,
  handleChange,
  defaultValue,
}: StepSliderProps) {
  return (
    <Flex width="100%" px={0} center>
      <Text color={textColor} bold sx={{ width: '60px' }}>
        {text}
      </Text>
      <Slider
        step={10}
        min={10}
        max={10 * values.length}
        getAriaValueText={(v) => values[v]}
        defaultValue={(values.indexOf(defaultValue) + 1) * 10}
        marks={marks}
        sx={{ maxWidth: '180px', mx: '20px', color: textColor }}
        onChange={handleChange}
      />
    </Flex>
  );
}
