import { Box, Chip } from '@mui/material';
import Text from '../../UI/atoms/Text';
import theme from '../../../styles/theme';

interface ReasonChipProps {
  reason: string;
  rating: number;
  isFirst: boolean;
}

const colors = [
  theme.colors.RED_LIGHT,
  theme.colors.YELLOW_LIGHT,
  theme.colors.GREEN_LIGHT,
];

export default function ReasonChip({
  reason,
  rating,
  isFirst,
}: ReasonChipProps) {
  const bgColor = colors[Math.trunc(rating / 33)];
  return (
    <Box
      sx={{
        maxWidth: '100%',
        display: 'flex',
        alignItems: 'start',
        justifyContent: 'space-between',
        marginBottom: '4px',
      }}
    >
      <Chip
        label={<Text sx={{ wordBreak: 'break-word' }}>{reason}</Text>}
        sx={{
          backgroundColor: bgColor,
          width: 'calc(100% - 30px)',
        }}
      />
      <Text underlined={isFirst} xl bold color={isFirst ? 'BLUE' : 'GRAY40'}>
        {`${rating}`}
      </Text>
    </Box>
  );
}
