import { Box } from '@mui/material';
import Text from './Text';

interface TextButtonProps {
  children: string;
  onClick?: () => void;
  major?: boolean;
  minor?: boolean;
  type: 'faded' | 'highlighted';
}

export default function TextButton({
  children,
  onClick,
  type,
  major,
  minor,
}: TextButtonProps) {
  return (
    <Box
      sx={{
        display: 'inline',
        width: 'fit-content',
        cursor: 'pointer',
      }}
      onClick={onClick}
    >
      <Text
        sm={!!minor}
        md={!!major}
        color={type === 'faded' ? 'GRAY40' : 'COOL_PURPLE80'}
        underlined
        bold={type === 'highlighted'}
      >
        {children}
      </Text>
    </Box>
  );
}
